import React,{useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack,Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const AddNewLine = ({ open, onClose, data, setData, onSubmit, loading }) => {
  const USER_SCHEMA = [
    {
      title: "Name",
      placeholder: "Enter Name",
      type: SCHEMA_TYPES.INPUT,
      name: "username",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Description",
      placeholder: "Enter description",
      type: SCHEMA_TYPES.INPUT,
      name: "email",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Product Division",
      placeholder: "",
      type: SCHEMA_TYPES.SELECT,
      name: "fullname",
      validator: VALIDATORS.REQUIRED,
    },
  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
        wrapperClasses="px-6"
    >
      <h3 className="opacity-80">ADD NEW LINE</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
       <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={onSubmit} loading={loading}>
         <Typography className="fw-20-500-24">{t('create')}</Typography> 
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddNewLine;
