import React, { useState } from "react";
import "./Table.css";

import {
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  FormControl,
  MenuItem,
  Select,
  Stack
} from '@mui/material'

const TableComponent = () => {
  const [selectedMachines, setSelectedMachines] = useState({});
  const [checkedRows, setCheckedRows] = useState({});
  const [selectAll, setSelectAll] = useState(false);
  const [isHeaderDropdownVisible, setIsHeaderDropdownVisible] = useState(false);

  const orders = [
    { id: 1, circuitCode: 'M001', circuitName: 'Material A', qty: 100 },
    { id: 2, circuitCode: 'M002', circuitName: 'Material B', qty: 200 },
    { id: 3, circuitCode: 'M003', circuitName: 'Material C', qty: 150 },
    { id: 4, circuitCode: 'M0003', circuitName: 'Material i', qty: 150 },


  ];

  const handleMachineChange = (orderId, newMachine) => {
    setSelectedMachines((prevState) => {
      const updatedState = {
        ...prevState,
        [orderId]: newMachine,
      };
      // Log the selected row data with machine when no checkboxes are checked
      if (!Object.values(checkedRows).some(Boolean)) {
        const selectedRow = orders.find((order) => order.id === orderId);
        console.log({
          ...selectedRow,
          selectedMachine: newMachine
        });
      }
      return updatedState;
    });
  };

  const handleSelectAll = (event) => {
    const isChecked = event.target.checked;
    setSelectAll(isChecked);

    const updatedCheckedRows = {};
    orders.forEach((order) => {
      updatedCheckedRows[order.id] = isChecked;
    });
    setCheckedRows(updatedCheckedRows);

    // Update header dropdown visibility based on select all
    setIsHeaderDropdownVisible(isChecked);
  };

  const handleCheckboxChange = (orderId) => {
    setCheckedRows((prevState) => {
      const updatedCheckedRows = {
        ...prevState,
        [orderId]: !prevState[orderId],
      };

      // Update header dropdown visibility based on any checked checkbox
      const anyChecked = Object.values(updatedCheckedRows).some(Boolean);
      setIsHeaderDropdownVisible(anyChecked);

      // Only set selectAll to true if ALL rows are checked
      const allChecked = orders.every((order) => updatedCheckedRows[order.id] === true);
      setSelectAll(allChecked);

      return updatedCheckedRows;
    });
  };

  const handleHeaderMachineChange = (newMachine) => {
    // Log the data of all selected rows with their machines when a machine is selected from the header dropdown
    const selectedRows = orders
      .filter((order) => checkedRows[order.id])
      .map(order => ({
        ...order,
        selectedMachine: newMachine
      }));
    console.log(selectedRows);

    // Update the selected machine for all checked rows
    const updatedMachines = { ...selectedMachines };
    selectedRows.forEach((row) => {
      updatedMachines[row.id] = newMachine;
    });
    setSelectedMachines(updatedMachines);
  };
  return (
    <div
    //  className="material-table"
     >
      <TableContainer
        // className="table-container"
        // sx={{
        //   marginTop: 2,
        // }}
      >
        <Table
          sx={{
            // borderSpacing: "0 0px",
            // borderCollapse: "separate",
          }}
        >
          <TableHead>
            <TableRow sx={{ backgroundColor: "#D9D9D9", justifyContent: 'space-between' }}>
              <TableCell
                sx={{ width: '20%',padding:1 }}
                className="fw-700"
              >
                <Checkbox checked={selectAll} onChange={handleSelectAll} /> S.
                No.
              </TableCell>
              <TableCell
                sx={{ width: '20%',padding:1 }}
                className="fw-700"
              >
                Circuit Code
              </TableCell>
              <TableCell
                sx={{ width: '20%',padding:1 }}
                className="fw-700"
              >
                Circuit Name
              </TableCell>
              <TableCell
                sx={{ width: '20%',padding:1 }}
                className="fw-700"
              >
                Qty
              </TableCell>
              <TableCell
                className="fw-700"
                sx={{ width: '20%',padding:1 }}
              >
                {isHeaderDropdownVisible ? (
                  // <Stack sx={{justifyContent:'flex-end',alignItems:'flex-end'}}>
                  <FormControl
                    sx={{
                      width: "200px",
                      borderRadius: "8px",
                      '& .MuiSelect-select': {
                        padding: '10px 12px',
                      },
                    }}
                  >
                    <Select
                      value={""}
                      displayEmpty
                      onChange={(e) =>
                        handleHeaderMachineChange(e.target.value)
                      }
                      inputProps={{ "aria-label": "Machine" }}
                    >
                      <MenuItem value="">Select Machine</MenuItem>
                      <MenuItem value="Machine 1">Machine 1</MenuItem>
                      <MenuItem value="Machine 2">Machine 2</MenuItem>
                      <MenuItem value="Machine 3">Machine 3</MenuItem>
                      <MenuItem value="Machine 4">Machine 4</MenuItem>
                      <MenuItem value="Machine 5">Machine 5</MenuItem>
                    </Select>
                  </FormControl>
                  // </Stack>
                ) : (
                  'Action'
                )}
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {orders.map((order, index) => (
              <TableRow
                key={order.id}
                sx={{
                  backgroundColor: "",
                  // height: "10px",
                  borderRadius: "8px",
                  marginBottom: "10px",
                  "&:last-child td": { border: "none" },
                }}
              >
                <TableCell>
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      gap: "8px",
                    }}
                    className="fw-500"
                  >
                    <Checkbox
                      checked={!!checkedRows[order.id]}
                      onChange={() => {
                        handleCheckboxChange(order.id);
                        // console.log("Checkbox changed")
                      }}
                    />
                    {index + 1}
                  </div>
                </TableCell>
                <TableCell className="fw-500">{order.circuitCode}</TableCell>
                <TableCell className="fw-500">{order.circuitName}</TableCell>
                <TableCell className="fw-500">{order.qty}</TableCell>
                {!isHeaderDropdownVisible ? (
                  <TableCell className="fw-500">
                    <FormControl
                      sx={{
                        width: "200px",
                        // marginLeft: -10,
                        borderRadius: "8px",
                        '& .MuiSelect-select': {
                          padding: '10px 12px',
                        },
                      }}
                    >
                      <Select
                        value={selectedMachines[order.id] || ""}
                        displayEmpty
                        onChange={(e) =>
                          handleMachineChange(order.id, e.target.value)
                        }
                        inputProps={{ "aria-label": "Machine" }}
                      >
                        <MenuItem value="">Select Machine</MenuItem>
                        <MenuItem value="Machine 1">Machine 1</MenuItem>
                        <MenuItem value="Machine 2">Machine 2</MenuItem>
                        <MenuItem value="Machine 3">Machine 3</MenuItem>
                        <MenuItem value="Machine 4">Machine 4</MenuItem>
                        <MenuItem value="Machine 5">Machine 5</MenuItem>
                      </Select>
                    </FormControl>
                  </TableCell>
                ) : (
                  <TableCell className="fw-500">-</TableCell>
                )}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default TableComponent;